/*----------------------------------\
          _recomendation
 \---------------------------------*/
 @use '../lib/variables.scss' as vars;
 @use '../lib/placeHolders.scss' as ph;
 @use '../lib/mixins.scss' as mixs;

.blockRecomendation{
    width: 100%;
    height: auto;
    background-color: vars.$LightGrayishBlue;
    padding: 6em 0;
    display: flex;
    flex-direction: column;
    text-align: center;
    &__data{
      padding: 0 3em;
      margin-bottom: 3em;
    }
    h2{
      margin-bottom: 1em;
      @extend %h2-text;
    }
    p{
      @extend %p-text;
    }
    &__cards{
      padding: 0 3em;
      img{
      margin-bottom: 0.5em;
      height: 70px;
      width: 70px;
      }
      h3{
      margin-bottom: 1em;  
      @extend %h3-text;
      }
      p{
        margin-bottom: 1.5em;
      @extend %p-text;
      }
    }
}