/*----------------------------------\
          _ARTICLES
 \---------------------------------*/
 @use '../lib/variables.scss' as vars;
 @use '../lib/placeHolders.scss' as ph;
 @use '../lib/mixins.scss' as mixs;

 .blockArticles {
    width: 100%;
    height: auto;
    background-color: vars.$VeryLightGray;
    padding: 4em 0;
    text-align: center;
    h2 {
      margin-bottom: 1em;
      @extend %h2-text;
    }
    &__articles {
      width: 90%;
      margin: 0 auto;
      overflow: hidden;
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
      gap: 35px;
  
      .articleBox {
        background-color: vars.$white;
        border-radius: 10px;
        display: flex;
        // justify-content: center;
        flex-direction: column;
        width: 100%;
        height: auto;
        padding-bottom: 1em;
        text-align: start;
  
        .articleImg {
          width: 100%;
          max-width: 720px;
          height: auto;
          padding-bottom: 15px;
          margin: 0 auto;
          img {
            object-fit: cover;
            border-top-left-radius: 5px;
            border-top-right-radius: 5px;
            width: 100%;
            height: 200px;
          }
        }
        &__data {
          padding: 0 2em;
          i {
            font-size: 1rem;
            font-weight: vars.$fontLite;
            line-height: vars.$p-height;
            padding-bottom: 0.5rem;
          }
          h3 {
            padding-bottom: 1rem;
            @extend %h3-text;
            &:hover {
              cursor: pointer;
              color: vars.$BrightCyan;
            }
          }
          p {
            font-size: 1.6rem;
            font-weight: vars.$fontLite;
            line-height: vars.$p-height;
          }
        }
      }
    }
  }