/*----------------------------\
       $Variables
 \----------------------------*/

//COLORS
//Primary Colors

$DarkBlue: hsl(233, 26%, 24%);
$DarkBlueO: rgba(45, 49, 77, 0.8);
$LimeGreen: hsl(136, 65%, 51%);
$BrightCyan: hsl(192, 70%, 51%);
$LimeGreenHover: hsl(136, 69%, 60%);
$BrightCyanHover: hsl(192, 88%, 60%);

//Nuetral Colors

$GrayishBlue: hsl(233, 8%, 62%);
$LightGrayishBlue: hsl(220, 16%, 96%);
$VeryLightGray: hsl(0, 0%, 98%);
$white: hsl(0, 0%, 100%);
$whiteO: rgba(255, 255, 255, 0.5);
$error: tomato;
$success: rgb(89, 227, 89);
$tableColorFont: #69737f;

//Gradientes Variables
$Gradient: linear-gradient(0.25turn, $LimeGreen, $BrightCyan);
$GradientHover: linear-gradient(0.25turn, $LimeGreenHover, $BrightCyanHover);

//FONT
$baseFont: "Public Sans", Sans-serif;

//FONT WEIGHT
$fontLite: 300;
$fontMedium: 400;
$fontBold: 500;
$fontBold__2: 600;

//FONT SIZE
$h1-size: 4.6rem;
$h2-size: 2.8rem;
$h3-size: 2rem;
$p-size: 1.6rem;
$footer-size: 1.5rem;

//LINE HEIGHT
$h-height: 2.6rem;
$p-height: 2.3rem;